import React, { useState } from "react";
import ReactDOM from "react-dom";
import logo from "../imgs/logo.jpeg";

import Home from './home';
import About from './about';
import ContactUs from './contactus';
// import Report1 from './report1';
// import Report2 from './report2';

import RacingInfo from './racingInfo';
import Report from './report';
import RacingNews from './racingNews';
import OfficialYearlyProgram from './officialYearlyProgram';

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
  //NavbarText
} from "reactstrap";

const Header = props => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);
  
  const dist = document.querySelector("#bodyContainer");
  const homePage = ()=> {
    //ReactDOM.render(<Body />,document.querySelector("#bodyContainer"));
  }

  const aboutPage = ()=> {
    ReactDOM.render(<aboutPage />,dist)
  }

  const contactusPage = ()=> {
    ReactDOM.render(<ContactUs />,dist)
  }
  let styleSeet = {
     color: "#fff",
     cursor: "pointer",
     fontWeight : 'bolder',
     fontSize: 17,
     margin:10
  }
  return (
    <div id="headerContainer">
      <img id="logo" src={logo} alt="" />
      
      <div id="NavBarContainer">
        <Navbar
          style={{ backgroundColor: "#333" }}//background-color: #eee3;
          //color="light"
          light
          expand="md"
        >
          {/* <NavbarBrand 
          //style={{ color: "#fff" }}
          style={{ color: "#fff", cursor: "pointer" }}
          onClick ={()=>ReactDOM.render(<Home />,document.querySelector("#bodyContainer"))}
          >
            الرئيسية
          </NavbarBrand> */}
          <NavbarToggler onClick={toggle} />
          <Collapse isOpen={isOpen} navbar>
            <Nav className="" navbar>
            <NavItem>
                <NavLink 
                style={styleSeet}
                onClick ={()=>ReactDOM.render(<Home />,document.querySelector("#bodyContainer"))}
                >
                  الرئيسية
                </NavLink>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle style={styleSeet} nav caret>
                  السباقات
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<RacingInfo allRacingInfo={[{title :'20191'},{title :'20192'}]} />,document.querySelector("#bodyContainer"))}
                  >
                    2019
                  </DropdownItem>
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<RacingInfo allRacingInfo={['20201','20202','20203']} />,document.querySelector("#bodyContainer"))}
                  >
                    2020
                  </DropdownItem>
                  <DropdownItem divider />
                </DropdownMenu>
              </UncontrolledDropdown>
              
              {/* <NavItem>
                <NavLink 
                //style={{ color: "#fff", cursor: "pointer" ,fontWeight : 'bolder'}}
                style={styleSeet}
                onClick ={()=>ReactDOM.render(<RacingNews allRacingNews={['خبر 1','خبر 2','خبر 3']} />,document.querySelector("#bodyContainer"))}
                >
                  اخبار الميدان
                </NavLink>
              </NavItem> */}
              
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle style={styleSeet} nav caret>
                  الخدمات
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='نموذج سحب جياد' />,document.querySelector("#bodyContainer"))}
                  >
                    نموذج سحب جياد
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='نموذج اضافة جياد' />,document.querySelector("#bodyContainer"))}
                  >
                    نموذج اضافة جياد
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='طلب برنت بالجوائز' />,document.querySelector("#bodyContainer"))}
                  >
                  طلب برنت بالجوائز
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='طلب اصدار شهادة حسن سيرة وسلوك' />,document.querySelector("#bodyContainer"))}
                  >
                    طلب اصدار شهادة حسن سيرة وسلوك
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='طلب بيان بالمراكز' />,document.querySelector("#bodyContainer"))}
                  >
                    طلب بيان بالمراكز
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='اصدار بطاقة مالك / مدرب / مساعد مدرب / خيال / سايس' />,document.querySelector("#bodyContainer"))}
                  >
                    اصدار بطاقة مالك / مدرب / مساعد مدرب / خيال / سايس
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='تسجيل الجياد المراد المشاركة بها' />,document.querySelector("#bodyContainer"))}
                  >
                    تسجيل الجياد المراد المشاركة بها
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='نموذج قسم ( سبب السحب )' />,document.querySelector("#bodyContainer"))}
                  >
                    نموذج قسم ( سبب السحب )
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='نتائج الحفلات' />,document.querySelector("#bodyContainer"))}
                  >
                    نتائج الحفلات
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='الأرشيف ( صور + فيديو )' />,document.querySelector("#bodyContainer"))}
                  >
                    الأرشيف ( صور + فيديو )
                  </DropdownItem>
                  
                  {/* <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report title='' />,document.querySelector("#bodyContainer"))}
                  >
                    
                  </DropdownItem>
                  <DropdownItem divider /> */}
                </DropdownMenu>
              </UncontrolledDropdown>

              <NavItem>
                <NavLink 
                style={styleSeet}
                onClick ={()=>ReactDOM.render(<OfficialYearlyProgram />,document.querySelector("#bodyContainer"))}
                >
                  {/* عن الميدان */}
                  البرنامج السنوي المعتمد
                </NavLink>
              </NavItem>

              {/* <NavItem>
                <NavLink 
                style={styleSeet}
                onClick ={()=>ReactDOM.render(<About />,document.querySelector("#bodyContainer"))}
                >
                  عن الميدان
                </NavLink>
              </NavItem> */}
              

              
              <NavItem>
                <NavLink
                  style={styleSeet}
                  onClick ={()=>ReactDOM.render(<ContactUs />,document.querySelector("#bodyContainer"))}
                >
                  تواصل معنا
                </NavLink>
              </NavItem>
              
            </Nav>
          </Collapse>
        </Navbar>
      </div>
      {/* <div id='line'></div> */}
    </div>
  );
};

export default Header;